.select-box {
  position: relative;
  display: block;
  width: 70%;
  margin: 0 auto;
  font-family: 'Open Sans', 'Helvetica Neue', 'Segoe UI', 'Calibri', 'Arial', sans-serif;
  font-size: 18px;
  background-color: #27282d;
  border: none;
  cursor: pointer;
  
  &__current {
    position: relative;
    cursor: pointer;
    outline: none;
    border: none;
    
    &:focus {
      & + .select-box__list {
        opacity: 1;
        border:none;
        

        animation-name: none;
        
        .select-box__option {
          cursor: pointer;
          border: none;
        }
      }
      
      .select-box__icon {
        transform: translateY(-50%) rotate(180deg);
        border: none;
      }
    }
  }
  
  &__icon {
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
    width: 20px;
    opacity: 0.3;
    transition: 0.2s ease;
    border: none;
  }
  
  &__value {
    display: flex;
    border: none;
    cursor: pointer;
  }
  
  &__input {
    display: none;
    border: none;
    cursor: pointer;
    
    &:checked + .select-box__input-text {
      display: block;
      border: none;
    }
  }
  
  &__input-text {
    display: none;
    width: 100%;
    margin: 0;
    padding: 15px;
    cursor: pointer;
    border: none;
  }
  
  &__list {
    position: absolute;
    width: 100%;
    padding: 0;
    list-style: none;
    opacity: 0;
    border: none;
    cursor: pointer;
    
    animation-name: HideList;
    animation-duration: 0.5s;
    animation-delay: 0.5s;
    animation-fill-mode: forwards;
    animation-timing-function: step-start;
  
  }
  
  &__option {
    display: block;
    padding: 15px;
    background-color: #27282d;
    border: none;
    cursor: pointer;
    
    &:hover,
    &:focus {
      color: #546c84;
      background-color: #27282d;
      border: none;
      cursor: pointer;
    }
  }
}

@keyframes HideList {
  from {
    transform: scaleY(1);
  }
  to {
    transform: scaleY(0);
  }
}

